.flip-card-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    gap: 8rem 10rem;
    max-width: 80%;
    margin: auto;
}

/* The flip card container */
.flip-card {
    background-color: transparent;
    width: 350px;
    height: 350px;
    perspective: 1000px;    
}

/* This container is needed to position the front and back side */
.flip-card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: transform 0.8s;
    transform-style: preserve-3d;
}

/* Do an horizontal flip when you move the mouse over the flip box container */
.flip-card:hover .flip-card-inner {
    transform: rotateY(180deg);
}

/* Position the front and back side */
.flip-card-front,
.flip-card-back {
    position: absolute;
    width: 350px;
    height: 350px;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    border-radius: 3px;
    box-shadow: .25rem .25rem .4rem rgba(0, 0, 0, 0.7);
}

/* Style the front side (fallback if image is missing) */
.flip-card-front {
    background-color: #bbb;
    color: black;
}

/* Style the back side */
.flip-card-back {
    background-color: #d66f54;
    color: #212c5f;
    transform: rotateY(180deg);
    height: 350px;
    width: 350px;
    font-weight: 700;
    padding: 3%;
}

/* button styling */
.pickMe-buttons {
    justify-content: center;
    text-align: center;
    background-color: #212c5f;
    font-size: 2vh;
    color: var(--secondaryColor);
    padding: 6px 8px;
    border-radius: 5px;
    cursor: pointer;
}

.pickMe-buttons:hover {
    color: var(--textColor);
    background-color: var(--secondaryColor);
}

.btn-container{
    margin-top: 3.75%;
}
